import React from "react";
import PlacesAutocomplete, {
  geocodeByAddress,
  getLatLng,
} from "react-places-autocomplete";
import Box from "@mui/material/Box";
import TextField from "@mui/material/TextField";

const Address = React.memo(({ address, setAddress, setCoordinates }) => {
  const handleSelect = async (value) => {
    const results = await geocodeByAddress(value);
    const latLng = await getLatLng(results[0]);
    setAddress(value);
    setCoordinates(latLng);
  };

  return (
    <Box component='form' fullWidth mt={"20px"}>
      <PlacesAutocomplete
        value={address}
        onChange={setAddress}
        onSelect={handleSelect}
        searchOptions={{ types: ["address"] }}
      >
        {({ getInputProps, suggestions, getSuggestionItemProps, loading }) => (
          <div>
            <TextField
              id='outlined-multiline-flexible'
              label='–í–≤–µ–¥—ñ—Ç—å –ø–æ–≤–Ω—É –∞–¥—Ä–µ—Å—É –≤–∞—à–æ—ó –Ω–µ—Ä—É—Ö–æ–º–æ—Å—Ç—ñ'
              fullWidth
              {...getInputProps({
                placeholder:
                  "–ì–æ–ª–æ—Å—ñ—ó–≤—Å–∫–∏–π –ø—Ä–æ—Å–ø–µ–∫—Ç, 112, –∫–≤–∞—Ä—Ç–∏—Ä–∞ 61, –ö–∏—ó–≤, –£–∫—Ä–∞—ó–Ω–∞",
              })}
            />

            <div>
              {loading ? <div>...loading</div> : null}

              {suggestions.map((suggestion, index) => {
                const style = {
                  backgroundColor: suggestion.active ? "#41b6e6" : "#fff",
                };

                return (
                  <div
                    {...getSuggestionItemProps(suggestion, { style })}
                    key={index}
                  >
                    {suggestion.description}
                  </div>
                );
              })}
            </div>
          </div>
        )}
      </PlacesAutocomplete>
    </Box>
  );
});

export { Address };
