import * as React from "react";
import Box from "@mui/material/Box";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";

const Categories = React.memo(({ categories, set–°ategories }) => {
  const handleChange = (event) => {
    switch (event.target.value) {
      case "Flat":
        set–°ategories([
          {
            id: 1,
            title: "Flat",
          },
        ]);
        break;
      case "Room":
        set–°ategories([
          {
            id: 2,
            title: "Room",
          },
        ]);
        break;

      default:
        set–°ategories([
          {
            id: 3,
            title: "House",
          },
        ]);
        break;
    }
  };

  return (
    <Box sx={{ minWidth: 120 }}>
      <FormControl fullWidth>
        <InputLabel id='demo-simple-select-label'>–©–æ –≤–∏ –∑–¥–∞—î—Ç–µ?</InputLabel>
        <Select
          labelId='demo-simple-select-label'
          id='demo-simple-select'
          value={categories[0].title}
          label='–í–∏–±–µ—Ä—ñ—Ç—å –∫–∞—Ç–µ–≥–æ—Ä—ñ—é'
          onChange={handleChange}
        >
          <MenuItem value={"Flat"}>–ö–≤–∞—Ä—Ç–∏—Ä–∞</MenuItem>
          <MenuItem value={"Room"}>–ö—ñ–º–Ω–∞—Ç–∞</MenuItem>
          <MenuItem value={"House"}>–ë—É–¥–∏–Ω–æ–∫</MenuItem>
        </Select>
      </FormControl>
    </Box>
  );
});

export { Categories };
